.stats-card-container {
    border-style: solid;
    border-width: 2px;
    border-color: rgba(220,220,220,0.6);
    padding: 5px 10px 5px 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    border-top: 0px;
    border-left: 0px;
}

.green-hr {
    display: block;
    -webkit-animation-name: stats-indicator-translate-to-green;
            animation-name: stats-indicator-translate-to-green;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    height: 3px;
    border: 0;
    border-top: 3px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

.red-hr {
    -webkit-animation-name: stats-indicator-translate-to-red;
            animation-name: stats-indicator-translate-to-red;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    display: block;
    height: 3px;
    border: 0;
    border-top: 3px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

.neutral-hr {
    display: block;
    height: 3px;
    border: 0;
    border-top: 3px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

.stats-card-trend {
     float : right;
}

.stats-card-title {
    font-size: 22px;
}

.stats-card-value {
    font-size: 24px;
}

.stats-card-trend {
    font-size: 18px;
}

.green-label {
    -webkit-animation-name: stats-label-to-green;
            animation-name: stats-label-to-green;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.red-label {
    -webkit-animation-name: stats-label-to-red;
            animation-name: stats-label-to-red;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.stats-card > div {
    padding-bottom: 0px !important;
}

.stats-card {
    margin-top: 15px;
}


