.header-container {
    padding: 0;
    background-color: purple;
    background:  -webkit-linear-gradient(90deg, #2655a0, #552072);
    background:  -webkit-gradient(linear, left top, right top, from(#2655a0), to(#552072));
    background:  -webkit-linear-gradient(left, #2655a0, #552072);
    background:  -o-linear-gradient(left, #2655a0, #552072);
    background:  linear-gradient(90deg, #2655a0, #552072);
    z-index: 5;
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
}


